import "./Contact.scss";
import Title from "../../shared/Title/Title";
import Social from "../home/components/Social";

import Footer from "../../shared/Footer/Footer";
import ScrollAnimation from "react-animate-on-scroll";

const socials = [
  {
    id: "twitch",
    url: "https://www.twitch.tv/kristoferyee",
  },
  {
    id: "twitter",
    url: "https://www.twitter.com/kristoferyee",
  },
  {
    id: "instagram",
    url: "https://www.instagram.com/kristoferyee/",
  },
  {
    id: "youtube",
    url: "https://www.youtube.com/kristoferyee",
  },
  {
    id: "youtube-secondary",
    url: "https://www.youtube.com/channel/UCQms6fAzw2VI77oHXDjdw9Q",
  },
];

export default function ContactScreen(props) {
  return (
    <div className="Contact" id={props.id}>
      <div className="arrows-container">
        <div className="arrows-bottom" />
      </div>
      <div className="content">
        <ScrollAnimation
          animateIn="animate__fadeInLeft"
          animateOut="animate__fadeOutLeft"
          offset={0}
        >
          <Title title="CONTACT" titleColor="white" />
        </ScrollAnimation>

        <ScrollAnimation
          animateIn="animate__fadeIn"
          animateOut="animate__fadeOut"
          offset={0}
        >
          <p className="content-quote">
            "Great things in business are never done by one person. They are
            done by a team of people."
          </p>
        </ScrollAnimation>

        <ScrollAnimation
          animateIn="animate__fadeIn"
          animateOut="animate__fadeOut"
          delay={100}
          offset={0}
        >
          <p className="content-quote">
            Feel free to contact me for a new project!
          </p>
        </ScrollAnimation>

        <div className="social-content">
          <ScrollAnimation
            animateIn="animate__fadeInLeft"
            animateOut="animate__fadeOutLeft"
            offset={0}
          >
            <div className="mail-content">
              <h1>Mail</h1>
              <p
                className="email"
                onClick={() => {
                  window.location = "mailto:kristoferyee@gmail.com";
                }}
              >
                kristoferyee@gmail.com
              </p>
            </div>
          </ScrollAnimation>

          <ScrollAnimation
            className="social-buttons"
            animateIn="animate__fadeIn"
            animateOut="animate__fadeOut"
            offset={0}
          >
            {socials.map((s) => (
              <div className="social-container">
                <Social
                  isCircular={false}
                  width={50}
                  social_id={s.id}
                  url={s.url}
                />
              </div>
            ))}
          </ScrollAnimation>
        </div>
      </div>

      <div className="footer">
        <Footer />
      </div>
    </div>
  );
}
