import "./Video.scss";

import thubmnail from "../../../assets/img/thumbnail-preview.svg";
import youtubeLogo from "../../../assets/img/youtube-logo-red.svg";

export default function Video(props) {
  const {
    imageUrl = thubmnail,
    title = "How NOT to build a PC - The Verge of Awful Computers",
    url = "https://www.youtube.com/watch?v=VE4Ruz_JEf0",
    publishedTime,
  } = props;

  // Takes an ISO time and returns a string representing how
  // long ago the date represents.
  function prettyDate(time) {
    const msPerMinute = 60 * 1000;
    const msPerHour = msPerMinute * 60;
    const msPerDay = msPerHour * 24;
    const msPerMonth = msPerDay * 30;
    const msPerYear = msPerDay * 365;

    const elapsed = new Date() - time;

    if (elapsed < msPerMinute) {
      return Math.round(elapsed / 1000) + " seconds ago";
    } else if (elapsed < msPerHour) {
      return Math.round(elapsed / msPerMinute) + " minutes ago";
    } else if (elapsed < msPerDay) {
      return Math.round(elapsed / msPerHour) + " hours ago";
    } else if (elapsed < msPerMonth) {
      if (Math.round(elapsed / msPerDay) === 2) {
        return "Yesterday";
      }
      return Math.round(elapsed / msPerDay) + " days ago";
    } else if (elapsed < msPerYear) {
      return Math.round(elapsed / msPerMonth) + " months ago";
    } else {
      return Math.round(elapsed / msPerYear) + " years ago";
    }
  }

  return (
    <div className="Video" onClick={() => window.open(url)}>
      <img width="100%" src={imageUrl} className="thumbnail" />
      <div className="description">
        <img width="50px" src={youtubeLogo} />
        <div className="content">
          <h1>{title}</h1>
          {publishedTime ? <p>{prettyDate(publishedTime)}</p> : null}
        </div>
      </div>
    </div>
  );
}
