import React, { useEffect, useState } from "react";
import { Link, animateScroll as scroll } from "react-scroll";

import "./Navbar.scss";

export default function Navbar(props) {
  let navbarClass = "Navbar bg-color";

  let listener = null;
  const [navClass, setNavClass] = useState("Navbar");

  useEffect(() => {
    listener = document.addEventListener("scroll", (scroll) => {
      let scrolled = document.scrollingElement.scrollTop;

      if (scrolled >= 10) {
        if (navClass !== "Navbar bg-color") {
          setNavClass("Navbar bg-color");
        }
      } else {
        if (navClass !== "Navbar") {
          setNavClass("Navbar");
        }
      }
    });
    return () => document.removeEventListener("scroll", listener);
  }, [navClass]);

  return (
    <div className={navClass}>
      <Link
        className="link"
        activeClass="active"
        to="home"
        spy={true}
        // hashSpy={true}
        smooth={true}
        offset={-70}
        duration={500}
      >
        Home
      </Link>

      <Link
        activeClass="active"
        className="link"
        to="latestvideo"
        spy={true}
        // hashSpy={true}
        smooth={true}
        offset={-70}
        duration={500}
      >
        Latest Video
      </Link>

      <Link
        activeClass="active"
        className="link"
        to="sponsors"
        spy={true}
        // hashSpy={true}
        smooth={true}
        offset={-70}
        duration={500}
      >
        Sponsors
      </Link>

      <Link
        activeClass="active"
        className="link"
        to="contact"
        spy={true}
        // hashSpy={true}
        smooth={true}
        offset={-70}
        duration={500}
      >
        Contact
      </Link>
    </div>
  );
}
