import ScrollAnimation from "react-animate-on-scroll";
import Title from "../../shared/Title/Title";
import "./About.scss";

export default function AboutScreen(props) {
  return (
    <div className="About" id={props.id}>
      <ScrollAnimation
        animateIn="animate__fadeInRight"
        animateOut="animate__fadeOutRight"
        offset={0}
      >
        <Title title="ABOUT" direction="right" />
      </ScrollAnimation>

      <ScrollAnimation
        animateIn="animate__fadeInLeft"
        animateOut="animate__fadeOutLeft"
        offset={0}
      >
        <p>
          Video Editor turned YouTuber. My content primarily stays in the tech
          space, but more focused on entertainment while still providing a
          learning experience and also constantly show how to make and save
          money in that space with an honest experience that I showcase to my
          viewers.
        </p>
      </ScrollAnimation>
    </div>
  );
}
