import React from "react";
import "./Title.scss";

export default function Title(props) {
  const {
    direction = "left",
    titleColor = "#3a3a3a",
    lineColor = "#ffaa00",
  } = props;

  const headerClass = direction === "left" ? "header-left" : "header-right";

  return (
    <div className="Text">
      <h1 className={headerClass} style={{ color: titleColor }}>
        {props.title}
      </h1>
      <div
        className="divider"
        style={{
          backgroundColor: lineColor,
          alignSelf: direction === "right" ? "flex-end" : "flex-start",
        }}
      />
    </div>
  );
}
