import "./Social.scss";

import twitter from "../../../assets/img/twitter-logo.svg";
import youtube from "../../../assets/img/youtube-logo.svg";
import twitch from "../../../assets/img/twitch-logo.svg";
import instagram from "../../../assets/img/instagram-logo.svg";
import youtube_secondary from "../../../assets/img/youtube_secondary-logo.svg";

export default function Social(props) {
  const {
    width = 150,
    social_id,
    isCircular = true,
    url = "https://www.twitch.tv/kristoferyee",
  } = props;

  const classContainer = isCircular ? "social-border" : "social";
  /** get logo */
  const getLogo = (social) => {
    switch (social) {
      case "twitter":
        return twitter;
      case "youtube":
        return youtube;
      case "youtube-secondary":
        return youtube_secondary;
      case "twitch":
        return twitch;
      case "instagram":
        return instagram;
      default:
        return twitter;
    }
  };

  return (
    <div className={classContainer} onClick={() => window.open(url)}>
      <img style={{ width, height: width }} src={getLogo(social_id)} />
    </div>
  );
}
