import React from "react";
import "./Sponsor.scss";

import Title from "../../shared/Title/Title";
import rslLogo from "../../assets/img/rsl-logo.svg";
import ScrollAnimation from "react-animate-on-scroll";

const RSL_LINK = `https://plarium.com/landings/en/desktop/raid/athel_f036_droapp?plid=403321&pxl=streamelements&publisherId=5bfb7d1ddea4545e6de4a3b4-Package3&inviterId=um000`;

export default function SponsorScreen(props) {
  return (
    <div id={props.id} className="Sponsor">
      <div className="arrow" />

      <ScrollAnimation
        animateIn="animate__fadeInRight"
        animateOut="animate__fadeOutRight"
        duration={0.5}
      >
        <div style={{ padding: 38 }}>
          <Title
            title="SPONSORS"
            direction="right"
            titleColor={"white"}
            lineColor={"#3a3a3a"}
          />
        </div>
      </ScrollAnimation>
      <div
        style={{
          display: "flex",
          flex: 1,
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <ScrollAnimation animateIn="animate__fadeIn">
          <img
            className="sponsor-image"
            src={rslLogo}
            onClick={() => window.open(RSL_LINK)}
          />
        </ScrollAnimation>
      </div>
    </div>
  );
}
