import React, { useState, useEffect } from "react";
import "./App.scss";

import HomeScreen from "./screens/home/Home";
import SponsorScreen from "./screens/sponsors/Sponsor";
import LatestVideoScreen from "./screens/latest_vid/LatestVideo";
import ContactScreen from "./screens/contact/Contact";
import AboutScreen from "./screens/about/About";

import Navbar from "./shared/Navbar/Navbar";
import "animate.css/animate.min.css";

import SplashScreen from "./screens/splash/Splash";

import "./config/firebase";
import RetrieveLatestVideoAPI from "./services/getLatestVideos";

function App() {
  const [width, setWindowWidth] = useState(0);
  const [apiState, setYoutubeData] = useState({ isLoading: true, data: {} });

  useEffect(() => {
    updateDimentions();
    window.addEventListener("resize", updateDimentions);

    return () => window.removeEventListener("resize", updateDimentions);
  }, []);

  useEffect(() => {
    try {
      RetrieveLatestVideoAPI()
        .then((videos) => {
          setYoutubeData({
            isLoading: false,
            data: videos,
          });
        })
        .catch((err) => {
          console.error("Error fetching youtube api: ", err);
          setYoutubeData({
            isLoading: false,
            data: {
              mainChannel: [],
              secondChannel: [],
            },
          });
        });
    } catch (err) {
      console.error("Error fetching youtube api: ", err);

      setYoutubeData({
        isLoading: false,
        data: {
          mainChannel: [],
          secondChannel: [],
        },
      });
    }
  }, []);

  const updateDimentions = () => {
    const width = window.innerWidth;
    setWindowWidth(width);
  };

  const responsive = {
    isMobile: width < 1023,
  };

  return (
    <div className="App">
      <SplashScreen visible={apiState.isLoading} />

      <div style={{ display: apiState.isLoading ? "none" : "" }}>
        <Navbar />

        <div className="content">
          <HomeScreen id="home" />
          <AboutScreen id="about" />
          <LatestVideoScreen
            id="latestvideo"
            isMobile={responsive.isMobile}
            mainChannel={apiState.data.mainChannel}
            secondChannel={apiState.data.secondChannel}
          />
          <SponsorScreen id="sponsors" />
          <ContactScreen id="contact" />
        </div>
      </div>
    </div>
  );
}

