import React from "react";

import "./Splash.scss";

import animation from "../../assets/animation/logo.gif";

export default function SplashScreen(props) {
  const { visible } = props;
  const classname = visible ? "Splash fadeIn" : "Splash fadeOut";

  return (
    <div className={classname}>
      <div className="animation-container">
        <img src={animation} width="300px" />
      </div>
    </div>
  );
}
