import sleep from "../utils/timeout";

import firebase from "firebase/app";

const firestore = firebase.firestore();

export default async function getLatestVideos() {
  try {
    // placeholder

    // await sleep(3000);
    // return;

    const ref = firestore
      .collection("streamer")
      .doc("kristofer_yee")
      .collection("latest_video");

    const snapshot = await ref.orderBy("publishTime", "desc").get();
    const mainChannel = [];
    const secondChannel = [];

    snapshot.forEach((docSnapshot) => {
      const data = docSnapshot.data();

      switch (data.channelTitle) {
        case "Kristofer":
          secondChannel.push(data);
          break;
        case "KristoferYee":
          mainChannel.push(data);
          break;
      }
    });

    await sleep(2500);
    return {
      mainChannel: [...mainChannel],
      secondChannel: [...secondChannel],
    };
  } catch (err) {
    console.error("Youtube Retrieve API: ", err);

    throw err;
  }
}

async function getVideo(url) {
  try {
    const response = await fetch(url);
    const data = await response.json();

    if (!data) {
      return [];
    }

    // console.log("Data: ", data);
    const videos = data.items;

    // console.log("Videos: ", videos);

    return videos.map((v) => {
      const snippet = v.snippet;

      return {
        title: snippet.title,
        description: snippet.description,
        publishTime: snippet.publishTime,
        thumbnails: snippet.thumbnails.high,
      };
    });
  } catch (err) {
    throw err;
  }
}
