import React from "react";
import ScrollAnimation from "react-animate-on-scroll";

import Title from "../../shared/Title/Title";
import Video from "./components/Video";
import "./LatestVideo.scss";

export default function LatestVideoScreen(props) {
  const { isMobile, mainChannel = [], secondChannel = [] } = props;

  const data = [
    {
      id: "1",
      thumbnail: "asdasd.jpg",
      url: "https://www.youtube.com/watch?v=VE4Ruz_JEf0",
    },
    {
      id: "2",
      thumbnail: "asdasd.jpg",
      url: "https://www.youtube.com/watch?v=VE4Ruz_JEf0",
    },
    {
      id: "3",
      thumbnail: "asdasd.jpg",
      url: "https://www.youtube.com/watch?v=VE4Ruz_JEf0",
    },
  ];

  // const youtubeVideo = isMobile ? data.slice(0, 1) : data.slice(0, 3);

  const mainChannelVideo = isMobile
    ? mainChannel.slice(0, 1)
    : mainChannel.slice(0, 3);

  const secondChannelVideo = isMobile
    ? secondChannel.slice(0, 1)
    : secondChannel.slice(0, 3);

  if (!mainChannel.length && !secondChannel.length) {
    return <div></div>;
  }

  // console.log(mainChannelVideo);

  return (
    <div className="LatestVideo" id={props.id}>
      <ScrollAnimation animateIn="animate__fadeInLeft">
        <Title title="LATEST VIDEO" />
      </ScrollAnimation>

      <div className="video-content">
        <ScrollAnimation
          className="channel-title"
          animateIn="animate__fadeIn"
          animateOut="animate__fadeOut"
          offset={20}
        >
          <p>KristoferYee</p>
          <div className="divider" />
        </ScrollAnimation>

        {mainChannelVideo.map((v, index) => (
          <ScrollAnimation
            className="video-container"
            animateIn="animate__fadeInRight"
            animateOut="animate_fadeOutRight"
            delay={index * 100}
            offset={20}
          >
            <Video
              title={v.title}
              imageUrl={v.thumbnails.url}
              url={`https://www.youtube.com/watch?v=${v.youtube_id}`}
              publishedTime={new Date(v.publishTime)}
            />
          </ScrollAnimation>
        ))}
      </div>

      <div className="video-content">
        <ScrollAnimation
          className="channel-title"
          animateIn="animate__fadeIn"
          animateOut="fadeOut"
          offset={30}
        >
          <p>Kristofer</p>
          <div className="divider" />
        </ScrollAnimation>

        {secondChannelVideo.map((v, index) => (
          <ScrollAnimation
            className="video-container"
            animateIn="animate__fadeInRight"
            animateOut="animate_fadeOutRight"
            delay={index * 300}
            offset={30}
          >
            <Video
              title={v.title}
              imageUrl={v.thumbnails.url}
              url={`https://www.youtube.com/watch?v=${v.youtube_id}`}
              publishedTime={new Date(v.publishTime)}
            />
          </ScrollAnimation>
        ))}
      </div>
    </div>
  );
}
