import React from "react";

import "./Home.scss";
// import triangle_01 from "../../assets/img/triangle-01.svg";
import triangle_02 from "../../assets/img/triangle_02.svg";
import kyee_img from "../../assets/img/kyee_img.svg";

import Social from "./components/Social";
import ScrollAnimation from "react-animate-on-scroll";

const socials = [
  {
    id: "twitch",
    url: "https://www.twitch.tv/kristoferyee",
  },
  {
    id: "twitter",
    url: "https://www.twitter.com/kristoferyee",
  },
  {
    id: "instagram",
    url: "https://www.instagram.com/kristoferyee/",
  },
  {
    id: "youtube",
    url: "https://www.youtube.com/kristoferyee",
  },
  {
    id: "youtube-secondary",
    url: "https://www.youtube.com/channel/UCQms6fAzw2VI77oHXDjdw9Q",
  },
];

export default function HomeScreen(props) {
  return (
    <div className="Home" id={props.id}>
      <div className="content">
        <ScrollAnimation
          animateIn="animate__fadeInDown"
          animateOut="animate__fadeOutDown"
          offset={0}
        >
          <h1>KRISTOFER YEE</h1>
        </ScrollAnimation>

        <ScrollAnimation
          animateIn="animate__fadeInDown"
          animateOut="animate__fadeOutDown"
          // duration={1}
          delay={200}
          offset={0}
        >
          <ul>
            <li>PC TECH</li>
            <li>BUDGET BUILDING</li>
            <li>GAMER</li>
          </ul>
        </ScrollAnimation>

        <ScrollAnimation
          className="description-text"
          animateIn="animate__fadeInLeft"
          offset={0}
        >
          <p>
            Video Editor turned Youtuber. My content primarly stay in the tech
            space, but more focused on entertainment while still providing a
            learning experience and also constantly show how to make and save
            money in that space with an honest experience that I showcase to my
            viewers.
          </p>
        </ScrollAnimation>
        <div className="social-container">
          {socials.map((social, index) => (
            <ScrollAnimation
              offset={0}
              animateIn="animate__fadeInDown"
              delay={index * (index * 15)}
            >
              <div className="social-container-margin">
                <Social width={50} social_id={social.id} url={social.url} />
              </div>
            </ScrollAnimation>
          ))}
        </div>
      </div>

      <ScrollAnimation
        className="img-profile"
        animateIn="animate__fadeIn"
        duration={1}
        offset={0}
      >
        <img src={kyee_img} className="image" />
      </ScrollAnimation>

      {/* <div style={{ position: "relative", height: "100%" }}>
        <img src={triangle_01} className="triangle-01" />
      </div> */}
      <img src={triangle_02} className="triangle-02" />
    </div>
  );
}
